import React from 'react';
import Header from '../../Components/Header';
import styles from './AboutMe.module.css';
import Familia from '../../Assets/images/Familia.jpg';
import { langTokens, useTheme } from '../../ThemeContext';

const AboutMe = () => {
  const { darkTheme, language } = useTheme();
  const langVar = langTokens(language)['aboutMe'];
  return (
    <section
      className={`${darkTheme ? styles.darkTheme : ''} container`}
      id="about"
    >
      <div className={styles.header}>
        <Header primary={langVar['header']} secondary={langVar['header2']} />
      </div>
      <div className={styles.content}>
        <div id={styles.aboutMe}>
          <img src={Familia} alt="/" />
          <div className={styles.context}>
            <p>{langVar['aboutMeP1']}</p>
            <p>{langVar['aboutMeP2']}</p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutMe;
