import React from 'react';
import { useTheme } from '../ThemeContext';
import styles from './Header.module.css';

const Header = ({ primary, secondary }) => {
  const { darkTheme } = useTheme();

  return (
    <div
      className={`
    ${styles.header}
    ${darkTheme ? styles.darkTheme : ''}`}
    >
      <h1>{primary}</h1>
      <h2>{secondary}</h2>
    </div>
  );
};

export default Header;
