import React from 'react';
import Button from '../../Components/Button';
import Header from '../../Components/Header';
import { langTokens, useTheme } from '../../ThemeContext';
import styles from './Contact.module.css';
import { useForm } from 'react-hook-form';

const Contact = () => {
  const { darkTheme, language } = useTheme();
  const langVar = langTokens(language)['contact'];
  const {
    register,
    trigger,
    formState: { errors },
  } = useForm();

  const onSubmit = async (e) => {
    const isValid = await trigger();
    if (!isValid) {
      e.preventDefault();
    }
  };

  return (
    <div>
      <section
        className={`${darkTheme ? styles.darkTheme : ''}
        ${styles.container} container`}
        id="contact"
      >
        <div className={styles.header}>
          <Header primary={langVar['header1']} secondary={langVar['header2']} />
        </div>
        <div className={styles.content}>
          <form
            target="_blank"
            onSubmit={onSubmit}
            action="https://formsubmit.co/lehsanfrancisco@gmail.com"
            method="POST"
          >
            <label htmlFor="">{langVar['name']}</label>

            <input
              type="text"
              placeholder={langVar['name']}
              {...register('name', { required: true, maxLength: 100 })}
            />
            {errors.name && (
              <p>
                {errors.name.type === 'required' && 'This field is required'}
                {errors.name.type === 'maxLength' && 'Max length is 100 char.'}
              </p>
            )}

            <label htmlFor="">{langVar['email']}</label>

            <input
              type="text"
              placeholder={langVar['email']}
              {...register('email', {
                required: true,
                pattern: /^[A-Z0-9._%+-]+@[A-Z0_9.-]+\.[A-Z]{2,}$/i,
              })}
            />
            {errors.name && (
              <p>
                {errors.email.type === 'required' && 'This field is required'}
                {errors.email.type === 'pattern' && 'Invalid email address.'}
              </p>
            )}

            <label htmlFor="">{langVar['message']}</label>

            <textarea
              type="text"
              placeholder={langVar['MESSAGE']}
              rows="4"
              cols="50"
              style={{ resize: 'none' }}
              {...register('message', { required: true, maxLength: 2000 })}
            />
            {errors.name && (
              <p>
                {errors.message.type === 'required' && 'This field is required'}
                {errors.message.type === 'maxLength' &&
                  'Max length is 2000 char.'}
              </p>
            )}

            <Button type="submit" text={langVar['contact']} />
          </form>
        </div>
      </section>
    </div>
  );
};

export default Contact;
