import React from 'react';
import Header from '../../Components/Header';
import { langTokens, useTheme } from '../../ThemeContext';
import styles from './Skills.module.css';

const Skills = () => {
  const { darkTheme, language } = useTheme();
  const langVar = langTokens(language)['skills'];
  return (
    <section
      className={`${styles.container} ${
        darkTheme ? styles.darkTheme : ''
      } container`}
      id="skills"
    >
      <div className={styles.header}>
        <Header
          primary={langVar['skills'].toUpperCase()}
          secondary={langVar['skills']}
        />
      </div>
      <div className={styles.skills}>
        <div>
          <ul className={styles.professional}>
            <h3>
              {language === 'En' ? langVar['professional'] : langVar['skills']}{' '}
              <span>
                {language === 'En'
                  ? langVar['skills']
                  : langVar['professional']}
              </span>
            </h3>
            <li className={styles.html}>Html</li>
            <li className={styles.css}>Css</li>
            <li className={styles.javascript}>Javascript</li>
            <li className={styles.react}>React</li>
            <li className={styles.figma}>Figma</li>
          </ul>
          <ul className={styles.personal}>
            <h3>
              {language === 'En' ? langVar['pessoal'] : langVar['skills']}{' '}
              <span>
                {language === 'En' ? langVar['skills'] : langVar['pessoal']}
              </span>
            </h3>
            <li>{langVar['critical']}</li>
            <li>{langVar['problem']}</li>
            <li>{langVar['flex']}</li>
            <li>{langVar['results']}</li>
            <li>{langVar['motivation']}</li>
          </ul>
          <ul className={styles.languages}>
            <h3>
              {language === 'En' ? langVar['spoken'] : langVar['languages']}{' '}
              <span>
                {language === 'En' ? langVar['languages'] : langVar['spoken']}
              </span>
            </h3>
            <li>{langVar['primary']}</li>
            <p>{langVar['portuguese']}</p>
            <li>{langVar['secondary']}</li>
            <p>{langVar['english']}</p>
            <p>{langVar['spanish']}</p>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default Skills;
